import React, { Fragment, useState } from "react";
import "./index.css";
import {
  Popper,
  List,
  ListItem,
  ListSubheader,
  CircularProgress,
  ClickAwayListener,
} from "@material-ui/core";

import url from "../../../strings/url";

const SearchNotes = (props) => {
  const [searchQuery, setQuery] = useState("");
  const [elAnchor, setElAnchor] = useState(null);
  const [subjectResult, setSubject] = useState([]);
  const [topicResult, setTopic] = useState([{ topic: "IP Addressing" }]);
  const [isTopicFetching, setTF] = useState(false);
  const [isSubjectFetching, setSF] = useState(false);
  const [SFE, setSFE] = useState(false);
  const handleGetSubjects = (name) => {
    setSF(true);
    const headers = new Headers();
    headers.append("content-type", "application/json");
    headers.append("Authorization", localStorage.getItem("AnonymityToken#123"));
    fetch(`${url}/notes/subjects/${name}`, { headers, method: "GET" }).then(
      async (res) => {
        try {
          if (res.status === 404) {
            setSF(false);
            return setSubject([]);
          }
          if (res.ok) {
            const subjects1 = await res.json();

            setSubject(subjects1);
            return setSF(false);
          }
          if (res.status === 500) {
            setSFE(true);
            setSF(false);
          }
        } catch (e) {
          console.log("error parsing");
          setSubject([]);
          setSF(false);
        }
      }
    );
  };
  return (
    <div>
      <ClickAwayListener
        onClickAway={() => {
          setElAnchor(null);
        }}
        touchEvent={false}
      >
        <input
          placeholder="Search For Notes"
          autoComplete="off"
          className="myNotesSearchInputField"
          id="myNotesSearchInputField"
          value={searchQuery}
          onFocus={(e) => {
            setElAnchor(e.currentTarget);
          }}
          onChange={(e) => {
            setQuery(e.target.value);
            handleGetSubjects(e.target.value);
          }}
        ></input>
      </ClickAwayListener>

      <Popper
        open={Boolean(elAnchor)}
        anchorEl={elAnchor}
        style={{
          boxShadow: "0px 0px 10px rgba(0,0,0,0.1)",
          marginTop: 10,
          zIndex: 1300,
        }}
      >
        <List
          style={{
            width: 300,
            maxHeight: 200,
            overflowY: "auto",
            fontFamily: "poppins",
            fontWeight: "bold",
            fontSize: 13,
            background: "white",
          }}
        >
          <ListSubheader
            style={{
              fontWeight: "bold",
              fontSize: 16,
              background: "rgba(255,255,255,1)",
            }}
            disableSticky
          >
            Subjects
            {isSubjectFetching ? (
              <CircularProgress
                style={{
                  margin: 10,
                  height: 20,
                  width: 20,
                  color: "orange",
                  float: "right",
                }}
              ></CircularProgress>
            ) : (
              ""
            )}
          </ListSubheader>
          {subjectResult.length === 0 && !isSubjectFetching ? (
            searchQuery.length !== 0 ? (
              <ListItem className="backAndForth">No Result..</ListItem>
            ) : (
              <ListItem className="backAndForth">
                Why Don't You Try Writing Something??
              </ListItem>
            )
          ) : (
            subjectResult.map((element, i) => {
              return (
                <ListItem
                  className="comingFromLeftP5"
                  key={element.name}
                  style={{ animationDelay: `${(i - 1) * 0.05}s` }}
                  button
                  onClick={() => {
                    setElAnchor(null);
                    setQuery(element.name);
                    props.handleSubjectChange(element);
                  }}
                >
                  {element.name}
                  <div
                    style={{
                      fontSize: 10,
                      fontWeight: 300,
                      marginLeft: 10,
                    }}
                  >
                    {`${element.for.course.toUpperCase()}`}
                  </div>
                </ListItem>
              );
            })
          )}
        </List>
        <List
          style={{
            width: 300,
            maxHeight: 200,
            overflowY: "auto",
            fontFamily: "poppins",
            fontWeight: "bold",
            fontSize: 13,
            background: "white",
          }}
        >
          <ListSubheader
            style={{
              fontWeight: "bold",
              fontSize: 16,
              background: "rgba(255,255,255,1)",
            }}
          >
            Topic
            {isTopicFetching ? (
              <CircularProgress
                style={{
                  margin: 10,
                  height: 20,
                  width: 20,
                  color: "orange",
                  float: "right",
                }}
              ></CircularProgress>
            ) : (
              ""
            )}
          </ListSubheader>
          {topicResult.map((element) => {
            return (
              <ListItem
                key={element.topic}
                button
                onClick={() => {
                  setElAnchor(null);
                  props.handleTopicChange(element);
                }}
              >
                {element.topic}
              </ListItem>
            );
          })}
        </List>
      </Popper>
    </div>
  );
};

export default SearchNotes;
