import React, { Fragment, useState } from "react";
import { CenterStuff } from "../../../Person/OthersProfile/profile/Profile";
import { Button } from "@material-ui/core";
import ISM from "../insideSubjectModal/ISM";

const Subject = (props) => {
  const [subjectAndModule, setSubjectAndModule] = useState(null);
  return (
    <Fragment>
      {Boolean(subjectAndModule) ? (
        <ISM
          subjectAndModule={subjectAndModule}
          onClose={() => {
            setSubjectAndModule(null);
          }}
        ></ISM>
      ) : (
        ""
      )}
      <CenterStuff style={{ margin: 30 }}>
        <h1
          style={{
            fontFamily: "poppins",
            fontWeight: "bold",
            color: "grey",
            fontSize: 16,
          }}
        >
          {props.subject.name}
        </h1>
      </CenterStuff>
      <CenterStuff style={{ margin: 30 }}>
        <div
          style={{ fontWeight: "bolder", fontFamily: "poppins", fontSize: 14 }}
        >
          {props.subject.for.course.toUpperCase() +
            ` (${props.subject.for.branch.toUpperCase()})`}
        </div>
      </CenterStuff>
      {getList(props.subject.moduleCount).map((i) => {
        return (
          <CenterStuff key={i} style={{ margin: 10 }}>
            <Button
              className="fadeUp"
              variant="outlined"
              style={{
                padding: 10,
                animationDelay: `${(i - 1) * 0.1}s`,
                width: 300,
                fontFamily: "poppins",
                fontSize: 13,
              }}
              onClick={() => {
                setSubjectAndModule({ ...props.subject, module: i });
              }}
            >{`Module ${i}`}</Button>
          </CenterStuff>
        );
      })}
      <CenterStuff style={{ margin: 50 }}></CenterStuff>
    </Fragment>
  );
};
const getList = (count) => {
  let array = [];
  for (let i = 0; i < count; i++) {
    array.push(i + 1);
  }
  return array;
};
export default Subject;
