import React, { Fragment, useState } from "react";
import { connect } from "react-redux";
import url from "../../../../strings/url";
import { Modal, ModalContent, ModalActions, Header } from "semantic-ui-react";
import {
  List,
  ListItem,
  Avatar,
  Button,
  IconButton,
  Collapse,
  ListItemAvatar,
  ListItemText,
  ListItemSecondaryAction,
  CircularProgress,
  Divider,
} from "@material-ui/core";

import { Pagination } from "@material-ui/lab";
import { CenterStuff } from "../../../Person/OthersProfile/profile/Profile";
import { ArrowUpward } from "@material-ui/icons";
import { Spin, Result } from "antd";
import { ISMISM } from "./ISMISM/ISMISM";

const ISM = (props) => {
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState([]);
  const [firstTime, setFirstTime] = useState(true);
  const [totalCount, setTotalCount] = useState(0);
  const [selectedNote, setSelectedNote] = useState(null);
  const handleGetNotes = async (skip) => {
    setLoading(true);
    const headers = new Headers();
    headers.append("content-type", "application/json");
    headers.append("Authorization", localStorage.getItem("AnonymityToken#123"));
    const body = JSON.stringify(props.subjectAndModule);
    const res = await fetch(`${url}/notes/forModule/${skip}`, {
      method: "POST",
      body,
      headers,
    });
    if (res.ok) {
      const { result, count } = await res.json();
      setResult(result);
      setTotalCount(count);
      return setLoading(false);
    }
    setLoading(false);
  };
  if (firstTime) {
    handleGetNotes(0);
    setFirstTime(false);
  }

  return (
    <Fragment>
      {selectedNote ? (
        <ISMISM
          isUpVoted={selectedNote.upVotedBy.find(
            (element) => element.user.toString() === props.id
          )}
          isDownVoted={selectedNote.downVotedBy.find(
            (element) => element.user.toString() === props.id
          )}
          noteDetails={selectedNote}
          onClose={() => {
            setSelectedNote(null);
          }}
        ></ISMISM>
      ) : (
        ""
      )}
      <Modal
        open={Boolean(props.subjectAndModule)}
        onClose={() => {
          props.onClose();
        }}
        dimmer="inverted"
      >
        <ModalContent scrolling>
          <List>
            {loading ? (
              <Fragment>
                <CenterStuff>
                  <Spin
                    style={{ width: 100, height: 100, marginTop: 30 }}
                    size="large"
                  ></Spin>
                </CenterStuff>
                <div
                  style={{
                    fontWeight: "bold",
                    textAlign: "center",
                    marginTop: 40,
                  }}
                >
                  Fetching Notes For You....
                </div>
              </Fragment>
            ) : result.length === 0 ? (
              <Result
                status="info"
                title="No Notes For This Module Currently..."
              ></Result>
            ) : (
              result.map((e, i) => (
                <Fragment key={e}>
                  <ListItem
                    style={{
                      fontWeight: "bold",
                    }}
                    button
                    onClick={() => {
                      setSelectedNote(e);
                    }}
                  >
                    <ListItemAvatar>
                      <Avatar src={e.by.profilephoto}></Avatar>
                    </ListItemAvatar>
                    <ListItemText>
                      <div
                        style={{ fontWeight: "bold", fontFamily: "poppins" }}
                      >
                        {e.topic}
                      </div>
                    </ListItemText>

                    <ListItemSecondaryAction>
                      <CenterStuff style={{ justifyContent: "flex-end" }}>
                        <ArrowUpward style={{ marginRight: 6 }}></ArrowUpward>
                        <div>{e.upVotes}</div>
                      </CenterStuff>
                    </ListItemSecondaryAction>
                  </ListItem>
                  <Divider></Divider>
                </Fragment>
              ))
            )}
          </List>
        </ModalContent>
        {totalCount > 10 ? (
          <ModalContent style={{ display: "flex", justifyContent: "center" }}>
            <Pagination size="small" count={10}></Pagination>
          </ModalContent>
        ) : (
          ""
        )}
        <ModalActions>
          <Button
            style={{ marginBottom: 10 }}
            variant="outlined"
            color="secondary"
            onClick={() => {
              props.onClose();
            }}
          >
            Close
          </Button>
        </ModalActions>
      </Modal>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({ id: state.userReducer.id });

export default connect(mapStateToProps)(ISM);
