import "./index.css";
import url from "../../../strings/url";
import React, { useState, Fragment } from "react";
import { Modal, Dimmer } from "semantic-ui-react";
import { Upload } from "antd";
import {
  Button,
  TextField,
  CircularProgress,
  MenuItem,
  ThemeProvider,
  createMuiTheme,
  Popper,
  List,
  ListSubheader,
  ListItem,
  ClickAwayListener,
  Snackbar,
} from "@material-ui/core";
import {
  CloudUploadOutlined,
  SearchOutlined,
  ArrowForward,
  AddCircle,
} from "@material-ui/icons";

const handlePostNotes = (token, body) => {
  const headers = new Headers();
  headers.append("content-type", "application/json");
  headers.append("Authorization", token);
  return fetch(`${url}/post/notes`, {
    method: "POST",
    headers,
    body: JSON.stringify(body),
  });
};

const AddNotes = (props) => {
  const [isTF, setTF] = useState(false);
  const [formData, setFormDate] = useState({
    topic: "",
    forSubject: "",
    forModule: "",
    src: "",
  });
  const [error, setError] = useState(false);
  const [modalLoading, setModalLoading] = useState(false);
  let isFormNotValid =
    formData.forModule === "" ||
    formData.forSubject === "" ||
    formData.src === "" ||
    formData.topic === "";

  return (
    <Fragment>
      <Snackbar
        open={error}
        autoHideDuration={6000}
        onClose={() => {
          setError(false);
        }}
        anchorOrigin={{ horizontal: "center", vertical: "top" }}
        message="Error Posting Note"
      ></Snackbar>
      <Dimmer active={modalLoading} style={{ zIndex: 1309 }}>
        <CircularProgress
          style={{ width: 100, height: 100, color: "black" }}
        ></CircularProgress>
      </Dimmer>
      <Modal
        open={props.open}
        dimmer="inverted"
        onClose={() => {
          props.onClose();
        }}
      >
        <Modal.Header>Add Notes..</Modal.Header>

        <Modal.Content>
          <div style={{ padding: 10, fontFamily: "poppins" }}>
            Disclaimer !!! You Can't Add Notes For Course and Branch Other Than
            Yours
          </div>
          <MyAutoComplete
            setSandM={(s, m) => {
              setFormDate({ ...formData, forModule: m, forSubject: s });
            }}
          ></MyAutoComplete>
          <input
            placeholder="Enter The Topic Description..."
            value={formData.topic}
            onFocus={() => {
              setTF(true);
            }}
            onBlur={() => {
              setTF(false);
            }}
            onChange={(e) => {
              setFormDate({ ...formData, topic: e.target.value });
            }}
            style={{
              width: "90%",
              padding: 10,
              borderRadius: 10,
              boxShadow: `0px 0px ${isTF ? 10 : 1}px rgba(0, 0, 0, 0.05)`,
              border: "0.5px solid lightgrey",
              transition: "0.2s ease",
              outline: "none",
              fontWeight: "bold",
              fontSize: 13,
              margin: 10,
            }}
          ></input>
        </Modal.Content>
        <Modal.Content>
          <Upload
            accept="application/pdf"
            method="PUT"
            headers={{
              Authorization: localStorage.getItem("AnonymityToken#123"),
              contentType: "application/json",
            }}
            name="pdf"
            action={`${url}/upload/notes`}
            onChange={(info) => {
              if (info.file.status === "done") {
                setFormDate({ ...formData, src: info.file.response });
              }
            }}
            onRemove={() => {
              removeUploadedPhoto(
                localStorage.getItem("AnonymityToken#123"),
                formData.src.key
              );
              setFormDate({ ...formData, src: "" });
            }}
          >
            <Button variant="outlined">
              <CloudUploadOutlined
                style={{ marginRight: 4 }}
              ></CloudUploadOutlined>
              Upload Pdf
            </Button>
          </Upload>
        </Modal.Content>
        <Modal.Actions>
          <Button
            variant="contained"
            disableElevation
            disabled={isFormNotValid}
            color="primary"
            onClick={() => {
              setModalLoading(true);
              handlePostNotes(
                localStorage.getItem("AnonymityToken#123"),
                formData
              )
                .then(async (res) => {
                  if (res.ok) {
                    setModalLoading(false);
                    props.onSuccess();
                    return props.onClose();
                  }
                  setModalLoading(false);
                  setError(true);
                })
                .catch((e) => {
                  setModalLoading(false);
                  setError(true);
                });
            }}
            style={{
              marginRight: 10,
              marginBottom: 10,
              background: isFormNotValid
                ? "rgba(0,0,0,0.1)"
                : "rgba(135,206,255,1)",
            }}
          >
            Post Notes
          </Button>
          <Button
            variant="outlined"
            style={{ marginBottom: 10 }}
            onClick={() => {
              removeUploadedPhoto(
                localStorage.getItem("AnonymityToken#123"),
                formData.src.key
              );
              setFormDate({ ...formData, src: "" });
              props.onClose();
            }}
          >
            Exit
          </Button>
        </Modal.Actions>
      </Modal>
    </Fragment>
  );
};

const getList = (n) => {
  const a = [];
  for (let i = 0; i < n; i++) {
    a.push(i + 1);
  }
  return a;
};

const removeUploadedPhoto = (token, key) => {
  const headers = new Headers();
  headers.append("content-type", "application/json");
  headers.append("Authorization", token);
  return fetch(`${url}/uploaded/notes`, {
    method: "DELETE",
    headers,
    body: JSON.stringify({ key: key + "" }),
  });
};

export const MyAutoComplete = (props) => {
  const [anchorEl, setAnchorEl1] = useState(null);
  const [anchorEl2, setAnchorEl2] = useState(null);
  const [subject, setSubject] = useState(null);
  const [subjects, setSubjects] = useState([]);
  const [loading, setLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [module, setModule] = useState("");

  const handleGetSubjects = (name) => {
    setLoading(true);
    const headers = new Headers();
    headers.append("content-type", "application/json");
    headers.append("Authorization", localStorage.getItem("AnonymityToken#123"));
    setTimeout(() => {
      fetch(`${url}/postNotes/subjects/${name}`, {
        headers,
        method: "GET",
      }).then(async (res) => {
        try {
          if (res.status === 404) {
            setLoading(false);
            return setSubjects([]);
          }
          if (res.ok) {
            const subjects1 = await res.json();
            setSubjects(subjects1);

            return setLoading(false);
          }
          if (res.status === 500) {
            setLoading(false);
          }
        } catch (e) {
          console.log("error parsing");
          setSubjects([]);
          setLoading(false);
        }
      });
    }, 100);
  };
  return (
    <Fragment>
      <Fragment>
        <ClickAwayListener
          onClickAway={() => {
            setAnchorEl1(null);
          }}
          touchEvent={false}
        >
          <input
            onFocus={(e) => {
              setAnchorEl1(e.currentTarget);
            }}
            // onBlur={(e) => {
            //   setAnchorEl(null);
            // }}
            value={searchQuery}
            autoComplete="off"
            onChange={(e) => {
              handleGetSubjects(e.target.value);
              setSearchQuery(e.target.value);
              setSubject(null);
              setSubjects([]);
              setModule("");
              props.setSandM("", "");
            }}
            placeholder="Choose Subject"
            type="select"
            className="MySelectField"
          ></input>
        </ClickAwayListener>
        <ClickAwayListener
          onClickAway={() => setAnchorEl2(null)}
          touchEvent={false}
        >
          <input
            disabled={!Boolean(subject)}
            autoComplete="off"
            onFocus={(e) => {
              setAnchorEl2(e.currentTarget);
            }}
            value={module}
            placeholder="Choose Module"
            type="select"
            className="MySelectField"
          ></input>
        </ClickAwayListener>
      </Fragment>
      <Popper
        style={{
          zIndex: 1300,
          boxShadow: "0px 0px 10px rgba(0, 0, 0, 0.05)",
          margin: 10,
        }}
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
      >
        <List
          style={{
            background: "white",
            width: 300,
            maxHeight: 200,

            overflowY: "auto",
          }}
        >
          {loading ? (
            <ListItem>
              <CircularProgress
                style={{
                  color: "black",
                  height: 10,

                  width: 10,
                }}
              ></CircularProgress>
            </ListItem>
          ) : (
            ""
          )}
          {subjects.length > 0 ? (
            subjects.map((subject1) => {
              return (
                <ListItem
                  button
                  key={subject1.name}
                  onClick={() => {
                    console.log("hello");
                    setSearchQuery(subject1.name);
                    setSubject(subject1);
                    setAnchorEl1(null);
                  }}
                >
                  <ArrowForward
                    style={{ height: 10, width: 10, marginRight: 10 }}
                  ></ArrowForward>
                  {subject1.name}
                </ListItem>
              );
            })
          ) : searchQuery.length === 0 ? (
            <ListItem style={{ fontWeight: "bold" }}>
              <SearchOutlined style={{ marginRight: 20 }}></SearchOutlined>
              Try Writing Subject Name..
            </ListItem>
          ) : loading ? (
            ""
          ) : (
            <div style={{ margin: 20 }}>No Results...</div>
          )}
        </List>
      </Popper>
      <Popper
        style={{
          zIndex: 1300,
          boxShadow: "0px 0px 10px rgba(0, 0, 0, 0.05)",
          margin: 10,
        }}
        anchorEl={anchorEl2}
        open={Boolean(anchorEl2)}
      >
        <List
          style={{
            background: "white",
            width: 300,
            maxHeight: 200,

            overflowY: "auto",
          }}
        >
          {subject
            ? getList(subject.moduleCount).map((count) => {
                return (
                  <ListItem
                    button
                    key={count}
                    onClick={() => {
                      setModule(count);
                      props.setSandM(subject.name, count);
                    }}
                  >
                    <AddCircle
                      style={{ height: 10, width: 10, marginRight: 10 }}
                    ></AddCircle>
                    {count}
                  </ListItem>
                );
              })
            : ""}
        </List>
      </Popper>
    </Fragment>
  );
};

export default AddNotes;
